import { DataStorageService } from './../../../../../core/services/data.storage.service';
import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { FormListComponent } from '@fragments';
import { Company, PaymentType } from '@models';
import { ApiService, SweetAlertService } from '@services';
import { ATTACHMENT_TYPE, COMMON_FUNCTION, ENDPOINTS, STATUSES, SUBMISSION_TYPE } from '@shared';
import { VoucherReplenishment } from 'app/core/models/voucher-replenishment.model';
import { Voucher } from 'app/core/models/voucher.model';
import { FileStorageService } from 'app/core/services/file.storage.service';
import Swal from 'sweetalert2';
import { NgxSpinnerService } from 'ngx-spinner';


@Component({
  selector: 'app-company-voucher-fund',
  templateUrl: './company-voucher-fund.component.html',
  styleUrls: ['./company-voucher-fund.component.scss']
})
export class CompanyVoucherFundComponent implements OnInit, OnDestroy {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<Voucher>;
  dataSourceReplenishments: MatTableDataSource<VoucherReplenishment>;
  displayedColumns: string[] = ['company', 'fundAmount', 'voucherDate'];
  displayedReplenishmentColumns: string[] = ['transferedDate', 'amountTransfered', 'status'];
  @Input() safeURL: any;

  voucherReplenishment = new VoucherReplenishment();
  voucher = new Voucher();
  company = new Company();

  voucherReplenishmentList = [];
  companyList = [];
  attachmentDocument: any;
  statusList = [...STATUSES];

  voucherReplenishmentForm: FormGroup;
  currentUser: any;
  currentDate = new Date();
  selectedFiles: any;
  attachment: any;
  // safeURLVoucherReplenishment: any;
  previewDocument = false;
  process = 'save';
  remainingVoucherBalance = 0;

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private apiService: ApiService,
    private sweetAlertService: SweetAlertService,
    private sanitizer: DomSanitizer,
    private fileStorageService: FileStorageService,
    private dataStorage: DataStorageService
  ) {

  }

  ngOnInit(): void {
    this.currentUser = this.dataStorage.getUserAccount();
    this.setFormValidator();
    this.getCompanyList();
    this.getVoucherTrasnferList();
  }

  ngOnDestroy(): void {

  }

  setFormValidator(): void {
    this.voucherReplenishment.status = COMMON_FUNCTION.ACTIVE;
    this.voucherReplenishmentForm = this.formBuilder.group({
      amountTransfered: ['', [Validators.required]],
      company: ['', [Validators.required]],
      transferedDate: ['', [Validators.required]],
      // status: ['', [Validators.required]],
    })
  }

  resetFormValidator(): void {
    this.voucherReplenishment = new VoucherReplenishment();
    this.voucherReplenishment.status = COMMON_FUNCTION.ACTIVE;
    this.voucherReplenishmentForm.markAsUntouched();
    this.voucherReplenishmentForm.markAsPristine();
  }

  getCompanyList() {
    this.apiService.findAll(ENDPOINTS.company).subscribe((res: any) => {
      this.companyList = res.responseData.data;
    });
  }

  getCompanyDetails(res) {
    this.company = res;
  }


  getVoucherTrasnferDetails() {
    this.process = 'update';
    this.apiService.findById(ENDPOINTS.voucher, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        // this.voucherReplenishment.company = res.responseData.data.company.companyName;
        this.company = res.responseData.data.company;
        this.voucher.voucherAttachmentReference = res.responseData.data.voucherAttachmentReference;
        this.voucherReplenishment.company = this.company;
        this.remainingVoucherBalance = res.responseData.data.fundAmount;
        // this.voucherReplenishment.amountTransfered = 0;
        this.voucherReplenishment.transferedDate = res.responseData.data.voucherDate;
        this.voucherReplenishment.status = res.responseData.data.status;
        this.getVoucherTrasnferList();
        this.viewAttachmentFile();
        this.getCompanyReplenishmentList();
        this.previewDocument = true;
      }
    });
  }

  viewAttachmentFile() {
    this.apiService.findByParam(ENDPOINTS.attachmentDocument, `voucher-attachment-reference/` + this.voucher.voucherAttachmentReference)
      .subscribe((res1: any) => {
        if (res1) {
          this.attachmentDocument = res1.responseData.data;
          this.safeURL = this.sanitizer.bypassSecurityTrustResourceUrl(this.attachmentDocument.fileURL);
        }
      }, (err: any) => {
        this.attachmentDocument = [];
      });
  }

  getVoucherTrasnferList() {
    this.apiService.findAll(ENDPOINTS.voucher).subscribe((res: any) => {
      this.dataSource = new MatTableDataSource<Voucher>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    })
  }

  getCompanyReplenishmentList() {
    this.apiService.findAll(ENDPOINTS.voucherReplenishmentsByCompany+this.company?.id).subscribe((res: any) => {
      this.dataSourceReplenishments = new MatTableDataSource<VoucherReplenishment>(res.responseData.data);
      this.dataSourceReplenishments.paginator = this.formListComponent.paginator;
      this.dataSourceReplenishments.sort = this.formListComponent.sort;
    })
  }

  selectFile(event) {
    const attachmentSize = event.target.files[0].size;
    if (event.target.files[0].type === 'application/pdf') {
      if (attachmentSize > 15000000) { // 1MB = 1M
        this.sweetAlertService.customErrorMessage(`Attachments' total file size should not exceed 15MB.`);
        event.target.value = '';
        return;
      } else {
        const pdfURL = URL.createObjectURL(event.target.files[0]);
        this.safeURL = this.sanitizer.bypassSecurityTrustResourceUrl(pdfURL);
        this.previewDocument = true;
        const reader = new FileReader();
        const [file] = event.target.files;
        reader.readAsDataURL(file);
        reader.onload = () => {
        };
      }
      this.selectedFiles = event.target.files;
    } else {
      this.sweetAlertService.customErrorMessage('Attachment should be in PDF format only.');
      event.target.value = '';
      return;
    }
  }

  uploadVoucher() {
    Swal.fire({
      text: 'Are you sure you want to submit this VOUCHER?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        if (this.process === 'update') {
          this.saveVoucherReplenishment();
        } else {
          this.dataStorage.setDisplayProgressbar(true);
          let params = '';
          params = '?&attachmentType=' + ATTACHMENT_TYPE.VOUCHER_FUND + '&submissionType=' + SUBMISSION_TYPE.ORIGINAL;
          this.fileStorageService.pushFileToStorage(ENDPOINTS.attachmentDocument, this.selectedFiles.item(0), params)
            .subscribe(res => {
              const obj = res.responseData.data;
              this.voucherReplenishment.voucherAttachmentReference = obj.voucherAttachmentReference;
              this.voucherReplenishment.company = this.company;
              // this.voucher.voucherSignatories = this.signatoriesList;
              console.log('VOUCHER REPLENISHMENT', this.voucherReplenishment);
              this.saveVoucherReplenishment();
              this.selectedFiles = undefined;
            }, (err: any) => {
              this.sweetAlertService.error(err);
            }, () => {
              this.dataStorage.setDisplayProgressbar(false);
            });
        }
      }
    });
  }

  saveVoucherReplenishment() {
    this.apiService.save(ENDPOINTS.accountingVoucherFundTransfer, this.voucherReplenishment).subscribe((res: any) => {
      console.log(res);
      this.sweetAlertService.success(res);
      this.setFormValidator();
      this.getVoucherTrasnferList();
      this.resetFormValidator();
      this.process = 'save';
      this.previewDocument = false;
      this.attachment = null;
      this.router.navigateByUrl('asc/page/accounting/voucher-replenishment');
    }, (err) => {
      console.log(err);
      this.sweetAlertService.error(err);
    });
  }


  compareStatus(value: any, selected: any): boolean {
    if (value && selected) {
      return value === selected;
    }
  }


}
